<template>
	<view>
		<view style="content: ''; overflow: hidden;"></view>
		<view class="main">
			<view class="title">
				<u-input v-model="noticeTitle" type="text" :border-bottom="true" placeholder="请输入通知的标题" />
			</view>
			<u-line color="#e4e7ed"></u-line>
			<view class="content">
				<u-input v-model="noticeContent" placeholder="请输入此次通知的内容" maxlength='-1' type="textarea"
					:border-top="false" height="30" :auto-height="false" />
			</view>
		</view>
		<view class="main">
			<view class="desc">
				发送范围
			</view>
			<view class="send-class">
				<view class="class" v-for="(item, index) in classList" :key="index">
					<!-- <u-line :hair-line="false" margin="0 0 20rpx 0" color="#b2b4b8"></u-line> -->
					<u-gap height="8" bg-color="#f7f8fa"></u-gap>
					<view class="box">
						<u-checkbox size="50" label-size="22px" shape="circle" @change="checkboxChange"
							v-model="item.checked" :name="item.className">
							<view class="class-name">
								{{item.className}}
							</view>
						</u-checkbox>
					</view>
				</view>
			</view>
		</view>
		<view class="btn">
			<u-button @click="publishNotice" type="primary" shape="circle" :ripple="true" ripple-bg-color="#909399">确认发布
			</u-button>
		</view>
		<u-modal @confirm="backToIndex" v-model="show" :content="content"></u-modal>
	</view>
</template>

<script>
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				noticeTitle: '',
				noticeContent: '',
				tempList: [],
				classList: [],
				chooseClass:[],
				show:false,
				content:"",
				noticeId:""
			};
		},
		computed:{
			...mapState(['uid', 'currentRole', 'hasLogin', 'userInfo', 'hasAuth']),
		},
		onLoad() {
			this.tempList = uni.getStorageSync('currentRoleClassList')
			for (let item of this.tempList) {
				this.classList.push({
					className: item.class_name,
					classId: item._id,
					checked: false,
					disabled: false
				})
			}
		},
		methods: {
			backToIndex(){
				uni.reLaunch({
				    url: '../index/index'
				});
			},
			async publishNotice(){
				var time = new Date().getTime()
				console.log(this.$u.timeFormat(time, 'yyyy年mm月dd日 hh时MM分ss秒'))
				const db = uniCloud.database() //代码块为cdb
				await db.collection("class-notice")
					.add({
						type: '通知',
						title:this.noticeTitle,
						content: this.noticeContent,
						publisher_id: this.uid,
						class_id: this.chooseClass,
						release_time:time,
						confirmed:[]
					})
					.then((res) => {
						uni.showToast({
							title: '发布成功！'
						})
						console.log(res)
						this.noticeId = res.result.id
						this.show = true
						this.content = "班级资料发生改变，请返回首页重新进入！"
					})
					.catch((err) => {
						uni.showModal({
							content: err.message || '发布失败',
							showCancel: false
						})
					})
					.finally(() => {
				
					})
					for(let item of this.chooseClass){
						uniCloud.callFunction({
								name: 'notice-update-push',
								data: {
									id: item,
									notice_id: this.noticeId,
								}
							})
							.then(res => {
								console.log(res)
							});
					}
			},
			checkboxChange(e) {
				console.log(e);
				if(e.value){
					for(let i of this.classList){
						if(i.className == e.name){
							this.chooseClass.push(i.classId)
						}
					}
				}else{
					for(let i of this.classList){
						if(i.className == e.name){
							this.chooseClass.splice(this.chooseClass.findIndex(res => res === i.classId), 1)
						}
					}
				}
				console.log(this.chooseClass)
			},
		}
	}
</script>

<style>
	/* #ifndef H5 */
	page {
		height: 100%;
		background-color: #f7f8fa;
	}

	/* #endif */
</style>

<style lang="scss">
	.main {
		background-color: #ffffff;
		margin: 20rpx;
		margin-bottom: 30rpx;
		border-radius: 16rpx;
		box-shadow: 0 0 20upx 0 rgba(0, 0, 0, 0.10);
		box-sizing: border-box;
		overflow: hidden;
		padding: 10rpx 0;
	}

	.title,
	.content {
		margin: 10rpx 0;
		padding: 0 20rpx;
	}

	.desc {
		font-size: 18px;
		padding: 10rpx 30rpx;
	}

	.send-class {
		padding: 10rpx 0rpx;
	}
	
	.box{
		padding: 30rpx 30rpx 10rpx 30rpx;
	}

	.class {
		padding: 10rpx 0;

		.class-name {
			font-size: 16px;
		}
	}

	.btn {
		width: 400rpx;
		margin: 40rpx auto;
	}
</style>
